import { NextRequest, NextResponse } from "next/server";
import { getCategories, isPassword, scheduler, supabase } from "@api/props";
import { lostArkApi } from "../route";
import { deepCopy } from "@/utils";

export interface LostArkEventProp {
    title:string,
    img:string,
    link:string,
    explain: string,
    is_clear:boolean,
    date:{
        start:string,
        end:string,
        reward: string,
    }
}

export const initLostArkEventProp = {
    title: '',
    img: '',
    link: '',
    explain: `how to join`,
    is_clear:false,
    date:{
        start: '',
        end: '',
        reward: '',
    },
}

//! Í∞ÄÍ≥µ Î∂ÄÎ∂ÑÏóê Ïù¥Î≤§Ìä∏ Í∞ÄÍ≥µ ÍπåÏßÄ Ìï¥ÏïºÎê®
export const eventPatten = async (events:any): Promise<any> => {
    let contents:LostArkEventProp[] = [];
    try{
        /**
         * Ïπ¥ÌÖåÍ≥†Î¶¨Î•º Í∞ÄÏ†∏Ïò§Í∏∞
         * Ïπ¥ÌÖåÍ≥†Î¶¨Ïóê ÏûÖÎ†•Îêú Ïä§ÏºÄÏ•¥ÏùÑ Í∞ÄÏ†∏Ïò®Îã§
         * Ïä§ÏºÄÏ•¥Ïùò etcÏóêÏÑú ÏïÑÏù¥ÎîîÍ∞íÎì§ÏùÑ Í∞ÄÏ†∏ÏôÄÏÑú Ï≤¥ÌÅ¨ÌïúÎã§.
         * Ïª®ÌÖêÏ∏†Ïóî date: { start, end reward } Í∞Ä ÏûàÎã§.
         */
        const a = await scheduler({type:'LOA',name:'EVENT'},()=>{});


        for(const event of events){
            const initContents = deepCopy(initLostArkEventProp);
            contents.push({
                ...initContents,
                title: event.Title,
                img: event.Thumbnail,
                link: event.Link,
                date:{
                    start: event.StartDate,
                    end: event.EndDate,
                    reward: event.RewardDate,
                }
            })
        }
    }
    catch(error:any){
        
    }
    return contents;
}

export async function GET(req: NextRequest) {
    const password = req.headers.get("X-User-Password");
    if (password === null || !isPassword(password as string)) {
        return NextResponse.json(
            { message: "Unauthorized Request", data: [], },
            { status: 401 }
        );
    }
    try {
        const events = await lostArkApi.event();
        const response = await eventPatten(events);
        return NextResponse.json(
            { message: 'success', data: response, },
            {status: 200 }
        );
    }
    catch(error:any) { 
        return NextResponse.json(
            {message: 'Server Internal Error Occurred', data: [] },
            {status: 500, }
        );
    }
}

