import dayjs from "dayjs";
import duration from 'dayjs/plugin/duration';
import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';
import { createClient } from '@supabase/supabase-js'

dayjs.extend(duration);
dayjs.extend(utc);
dayjs.extend(timezone);

/**
 * ÎπÑÎ∞ÄÎ≤àÌò∏ Ïú†Ìö® Ïó¨Î∂Ä
 * @param pw password input
 * @returns password validity
 */
export const isPassword = (pw: string) => {
    return process.env.WEB_PASS_KEY === pw;
}

/** ÏäàÌçºÎ≤†Ïù¥Ïä§ Ïó∞Í≤∞ */
export const supabase = createClient(
    process.env.SUPABASE_API_BASE as string,
    process.env.SUPABASE_API_KEY as string
)

export const getCategories = async (type: string, name: string) => {
    let result = 0;
    try {
        const { data, error }: any = await supabase
            .from('categories')
            .select('id')
            .eq('type', type.toUpperCase()).eq('name', name.toUpperCase())
            .single();
        if (error) { throw error; } else { result = data.id; }
    }
    catch (error) { console.error(`getCategroies error:`, error); }
    finally { return result === 0 ? undefined : result }
}
export function scheduler(category: number, callback?: any): Promise<void>;
export function scheduler(options: { type: string, name: string }, callback?: any): Promise<void>;
export async function scheduler(option: number | { type: string, name: string }, callback?: any): Promise<void> {
    const todayAt = dayjs().tz('Asia/Seoul');
    let category: number | undefined = 0;
    try {
        if (typeof option === 'number') { category = option; }
        else if (typeof option === 'object' && option !== null) {
            category = await getCategories(option.type, option.name);
            if (category === undefined) {
                throw new Error(`not find categories`);
            }
        }
        const { data, error }: any = await supabase
            .from(`schedulers`)
            .select(`*`)
            .eq(`category`, category)
        console.log(data);
    }
    catch (error) { console.error(`scheduler error:`, error); }
}